	module.exports = {"data":{"markdownRemark":{"html":"<h2>What is A B Testing?</h2>\n<p>A/B testing (sometimes called split testing) is comparing two versions of a web page or element to see which one performs better. You compare two web pages or elements by showing the two variants (let’s call them A and B) to similar visitors at the same time. The one that gives a better conversion rate, wins!</p>\n<p>Every website wants visitors converting from just visitors to something else(Convertions, sign ups, or just take action for something). The rate at which a website is able to do this is its “conversion rate”. Measuring the performance of a variation (A or B) means measuring the rate at which it converts visitors to goal achievers. Data scientists use A/B testing to see which version(s) of their product leads to their desired goal faster. Sometimes large companies run hundreds or thousands of A/B tests to make product decisions. These tests are almost always randomized, large, and blinded. This means that the subject or user does not know abou the A/B test.</p>\n<h4>More Information:</h4>\n<!-- Please add any articles you think might be helpful to read before writing the article -->\n<p>For more information on A/B Testing please read\n<a href='https://vwo.com/ab-testing/' target='_blank'>The Complete Guide to A/B Testing</a> and\n<a href='https://hbr.org/2017/06/a-refresher-on-ab-testing' target='_blank'>A Refresher on A/B Testing</a>.\n<a href='https://www.optimizely.com/ab-testing/' target='_blank'>What is A/B Testing?</a></p>","fields":{"slug":"/user-experience-design/a-b-testing/"},"frontmatter":{"title":"A B Testing"}}},"pathContext":{"slug":"/user-experience-design/a-b-testing/"}}

